
'use client';

import type { Building } from '@/types';
import { useState, useEffect, useMemo } from 'react';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Search, Package, CalendarDays } from 'lucide-react';
import { Card, CardContent } from '@/components/ui/card';
import type { User } from '@/types';

const BUILDING_SIZES = ['小型', '中型', '大型'] as const;
const ALL_OPTIONS_VALUE = "__ALL_OPTIONS_FILTER__"; 

interface WorksFilterControlsProps {
  initialWorks: Building[];
  currentUser: User | null; // currentUser is needed for BuildingCard if rendered here, but we are removing rendering
  availableYears?: number[];
  showYearFilter?: boolean;
  showSizeFilter?: boolean;
  defaultSearchTerm?: string;
  onFilteredWorks: (filteredWorks: Building[]) => void;
}

export default function WorksFilterControls({
  initialWorks,
  // currentUser, // No longer needed here as BuildingCard rendering is removed
  availableYears: providedAvailableYears,
  showYearFilter = true,
  showSizeFilter = true,
  defaultSearchTerm = '',
  onFilteredWorks,
}: WorksFilterControlsProps) {
  const [searchTerm, setSearchTerm] = useState(defaultSearchTerm);
  const [selectedSize, setSelectedSize] = useState<string>(ALL_OPTIONS_VALUE); 
  const [selectedYear, setSelectedYear] = useState<string>(ALL_OPTIONS_VALUE);

  const availableYears = useMemo(() => {
    if (!showYearFilter) return [];
    if (providedAvailableYears && providedAvailableYears.length > 0) return providedAvailableYears.sort((a, b) => b - a);
    if (!initialWorks || initialWorks.length === 0) return [];
    const years = new Set(initialWorks.map(work => work.eventYear).filter(year => year != null));
    return Array.from(years).sort((a, b) => b - a);
  }, [initialWorks, providedAvailableYears, showYearFilter]);

  useEffect(() => {
    let filtered = [...initialWorks];

    if (searchTerm.trim()) {
      const lowerSearchTerm = searchTerm.toLowerCase();
      filtered = filtered.filter(
        (work) =>
          work.name.toLowerCase().includes(lowerSearchTerm) ||
          work.author.toLowerCase().includes(lowerSearchTerm) ||
          (work.description && work.description.toLowerCase().includes(lowerSearchTerm))
      );
    }

    if (showSizeFilter && selectedSize && selectedSize !== ALL_OPTIONS_VALUE) {
      filtered = filtered.filter((work) => work.size === selectedSize);
    }

    if (showYearFilter && selectedYear && selectedYear !== ALL_OPTIONS_VALUE) {
      filtered = filtered.filter((work) => work.eventYear === parseInt(selectedYear, 10));
    }
    onFilteredWorks(filtered);
  }, [searchTerm, selectedSize, selectedYear, initialWorks, showSizeFilter, showYearFilter, onFilteredWorks]);
  
  useEffect(() => {
    setSearchTerm(defaultSearchTerm);
    setSelectedSize(ALL_OPTIONS_VALUE);
    setSelectedYear(ALL_OPTIONS_VALUE);
    // The effect above will be triggered by initialWorks changing, and it will call onFilteredWorks.
  }, [initialWorks, defaultSearchTerm]);

  return (
    <Card className="mb-6 shadow-sm border-border/30 bg-card/50">
      <CardContent className="p-3 sm:p-4 space-y-3 md:space-y-0 md:flex md:flex-wrap md:items-end md:gap-3">
        <div className="flex-grow min-w-[180px] md:max-w-xs lg:max-w-sm">
          <Label htmlFor="search-works-filter" className="text-xs font-medium text-muted-foreground flex items-center mb-1">
            <Search className="mr-1.5 h-3.5 w-3.5" />
            搜索作品
          </Label>
          <Input
            id="search-works-filter"
            type="text"
            placeholder="名称、作者、描述..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="h-9 text-sm"
          />
        </div>

        {showSizeFilter && (
          <div className="min-w-[120px] sm:min-w-[140px] md:flex-shrink-0">
            <Label htmlFor="filter-size-filter" className="text-xs font-medium text-muted-foreground flex items-center mb-1">
              <Package className="mr-1.5 h-3.5 w-3.5" />
              规格
            </Label>
            <Select
              value={selectedSize} 
              onValueChange={(value) => {
                setSelectedSize(value);
              }}
            >
              <SelectTrigger id="filter-size-filter" className="h-9 text-sm">
                <SelectValue placeholder="所有规格" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value={ALL_OPTIONS_VALUE}>所有规格</SelectItem>
                {BUILDING_SIZES.map((s) => (
                  <SelectItem key={s} value={s}>
                    {s}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        )}

        {showYearFilter && availableYears.length > 0 && (
            <div className="min-w-[120px] sm:min-w-[140px] md:flex-shrink-0">
            <Label htmlFor="filter-year-filter" className="text-xs font-medium text-muted-foreground flex items-center mb-1">
              <CalendarDays className="mr-1.5 h-3.5 w-3.5" />
              年份
            </Label>
            <Select
              value={selectedYear}
              onValueChange={(value) => {
                  setSelectedYear(value);
              }}
            >
              <SelectTrigger id="filter-year-filter" className="h-9 text-sm">
                <SelectValue placeholder="所有年份" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value={ALL_OPTIONS_VALUE}>所有年份</SelectItem>
                {availableYears.map((year) => (
                  <SelectItem key={year} value={String(year)}>
                    {year}年
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
    
