
'use client';

import Image from 'next/image';
import { useRouter } from 'next/navigation';
import type { Building, User, BuildingStatus, Competition } from '@/types';
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { ShieldAlert, CalendarDays, Users, Ticket, ArrowRight, Eye, Flag, Loader2 } from 'lucide-react';
import { cn } from '@/lib/utils';
import { format, parseISO } from 'date-fns';
import { useToast } from '@/hooks/use-toast';
import { useState, useTransition } from 'react';
import { reportBuildingAction } from '@/app/actions';

interface BuildingCardProps {
  building: Building;
  competition?: Competition;
  currentUser: User | null;
}

const getStatusBadgeClass = (status?: BuildingStatus | Competition['status']) => {
  if (!status) return 'bg-muted text-muted-foreground';
  switch (status) {
    case '有效':
    case '已结束':
      return 'bg-green-500/20 text-green-400 border-green-500/30';
    case '等待审核':
      return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
    case '审查中-被举报':
      return 'bg-orange-500/20 text-orange-400 border-orange-500/30';
    case '进行中': 
    case '评审中': 
      return 'bg-blue-500/20 text-blue-400 border-blue-500/30';
    case '策划中': 
      return 'bg-purple-500/20 text-purple-400 border-purple-500/30';
    case '违规':
    case '作弊':
    case '无效':
    case '已归档':
      return 'bg-red-500/20 text-red-400 border-red-500/30';
    default:
      return 'bg-muted text-muted-foreground';
  }
};


export default function BuildingCard({ building, competition, currentUser }: BuildingCardProps) {
  const { toast } = useToast();
  const router = useRouter();
  const [isReporting, startReportTransition] = useTransition();
  const [isReportDialogOpen, setIsReportDialogOpen] = useState(false);
  const [reportReason, setReportReason] = useState('');

  const displayStatus = competition ? competition.status : building.status;
  const isViewable = competition ? true : building.status === '有效';
  const cardTitle = competition ? competition.name : building.name;
  const cardDescription = competition ? competition.description : building.description;
  const targetHref = competition ? `/history/competition/${competition.id}` : `/building/${building.id}`;
  
  let buttonText = competition ? "查看活动" : "查看作品";
  if (!competition && !isViewable) {
    buttonText = `状态: ${building.status}`;
  }


  let formattedDate = '日期未知';
  if (competition) {
    if (competition.startDate && competition.endDate) {
      try {
          formattedDate = `${format(parseISO(competition.startDate), 'MM/dd')} - ${format(parseISO(competition.endDate), 'MM/dd')}, ${competition.year}`;
      } catch (e) { console.error("Date formatting error for competition", e); formattedDate = `${competition.year}年`;}
    } else {
      formattedDate = `${competition.year}年`;
    }
  } else if (building.submissionDate) {
    try {
      formattedDate = format(new Date(building.submissionDate), 'yyyy年MM月dd日');
    } catch (e) { console.error("Date formatting error for building submission date", e); }
  } else if (building.eventYear) { 
     formattedDate = `${building.eventYear}年活动`;
  }
  
  const imageUrl = building.imageUrl || 'https://placehold.co/600x338.png';
  const aiHint = building.aiHint || (competition ? "event banner" : "minecraft architecture");

  const handleCardClick = (e: React.MouseEvent<HTMLDivElement>) => {
    // Prevent navigation if a button inside the card was clicked (like report button)
    if ((e.target as HTMLElement).closest('button, a[href]')) {
      return;
    }

    if (isViewable) {
      router.push(targetHref);
    } else if (!competition && building.status !== '有效') {
      toast({
        title: '访问受限',
        description: `此作品（状态：${building.status}）当前不可访问，请稍后再试。`,
        variant: 'default',
      });
    }
  };

  const handleReportSubmit = async () => {
    if (!currentUser) {
      toast({ title: "请先登录", description: "登录后才能举报作品。", variant: "default" });
      setIsReportDialogOpen(false);
      return;
    }
    if (reportReason.trim().length < 10) {
      toast({ title: "举报失败", description: "举报原因至少需要10个字符。", variant: "destructive" });
      return;
    }

    startReportTransition(async () => {
      const result = await reportBuildingAction(building.id, reportReason);
      if (result.success) {
        toast({ title: "举报成功", description: result.message });
      } else {
        toast({ title: "举报失败", description: result.message, variant: "destructive" });
      }
      setIsReportDialogOpen(false);
      setReportReason(''); 
    });
  };
  
  const canReport = currentUser && !competition && building.status === '有效';

  return (
    <Card 
      onClick={handleCardClick}
      className={cn(
        "bg-card border-border overflow-hidden shadow-lg hover:shadow-primary/20 transition-all duration-300 flex flex-col h-full group relative",
        isViewable ? 'cursor-pointer' : 'cursor-not-allowed' // Removed opacity-80 to let overlay handle it
      )}
      tabIndex={0} 
      onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') handleCardClick(); }}
      role="button"
      aria-label={`${competition ? '查看活动' : '查看作品'}: ${cardTitle}`}
    >
      <div className="relative aspect-[16/9] w-full overflow-hidden">
        <Image
          src={imageUrl.replace('800x600', '600x338').replace('600x400', '600x338')}
          alt={cardTitle}
          fill 
          sizes="(max-width: 640px) 100vw, (max-width: 768px) 50vw, (max-width: 1024px) 33vw, 25vw"
          className={cn(
            "object-cover transform transition-transform duration-300", 
            isViewable && "group-hover:scale-105"
          )}
          data-ai-hint={aiHint}
        />
        {displayStatus && (
          <Badge className={cn(
              "absolute top-3 right-3 px-2 py-1 text-xs font-semibold rounded-md shadow-md", 
              getStatusBadgeClass(displayStatus)
            )}>
            {displayStatus}
          </Badge>
        )}
        {!isViewable && !competition && (
          <div className="absolute inset-0 bg-destructive/30 backdrop-blur-sm flex flex-col items-center justify-center text-center p-4 z-10">
            <ShieldAlert className="h-10 w-10 text-destructive-foreground mb-2" />
            <p className="text-md font-semibold text-destructive-foreground">状态: {building.status}</p>
            <p className="text-xs text-destructive-foreground/80">此作品当前不可交互。</p>
          </div>
        )}
      </div>
      <CardHeader className="pt-4 pb-2 flex-shrink-0">
        <CardTitle className={cn(
          "text-lg font-semibold text-card-foreground leading-tight truncate", 
          isViewable && "group-hover:text-primary transition-colors"
        )}>
          {cardTitle}
        </CardTitle>
      </CardHeader>
      <CardContent className="flex-grow pt-1 pb-3 space-y-2 text-sm">
        <div className="flex items-center text-muted-foreground gap-1.5">
          <CalendarDays size={14} /> 
          <span>{formattedDate}</span>
        </div>
        {cardDescription && <p className="text-muted-foreground leading-relaxed line-clamp-2">{cardDescription}</p>}
      </CardContent>
      <CardFooter className="flex justify-between items-center mt-auto pt-3 pb-4 px-4 border-t border-border/50 flex-shrink-0">
        <div className="flex items-center gap-3 text-xs text-muted-foreground">
          {!competition && (
            <>
              <div className="flex items-center gap-1">
                <Users size={14} />
                <span>{building.votes} 票</span>
              </div>
              <div className="flex items-center gap-1">
                <Ticket size={14} />
                <span>ID: {building.id.substring(0,6).toUpperCase()}</span>
              </div>
            </>
          )}
          {competition && <span className="text-xs text-muted-foreground">活动代码: {competition.id.substring(0,6).toUpperCase()}</span>}
        </div>
        
        <div className="flex items-center gap-1">
            {canReport && (
              <AlertDialog open={isReportDialogOpen} onOpenChange={(open) => {
                if (!open) setReportReason(''); // Clear reason if dialog is closed
                setIsReportDialogOpen(open);
              }}>
                <AlertDialogTrigger asChild>
                  <Button
                      size="sm"
                      variant="ghost"
                      className="px-2 py-1 h-auto text-xs text-destructive hover:bg-destructive/10 hover:text-destructive"
                      onClick={(e) => { e.stopPropagation(); setIsReportDialogOpen(true); }}
                      aria-label={`举报作品 ${building.name}`}
                  >
                      <Flag size={14} />
                      <span className="ml-1 hidden sm:inline">举报</span>
                  </Button>
                </AlertDialogTrigger>
                <AlertDialogContent onClick={(e) => e.stopPropagation()}>
                  <AlertDialogHeader>
                    <AlertDialogTitle>举报作品: {building.name}</AlertDialogTitle>
                    <AlertDialogDescription>
                      请详细说明您的举报原因，这将有助于管理员审核。原因至少需要10个字符。
                    </AlertDialogDescription>
                  </AlertDialogHeader>
                  <div className="space-y-2 py-2">
                    <Label htmlFor={`reportReasonTextarea-${building.id}`} className="text-sm font-medium">举报原因</Label>
                    <Textarea
                      id={`reportReasonTextarea-${building.id}`}
                      placeholder="请在此处输入详细的举报理由..."
                      value={reportReason}
                      onChange={(e) => setReportReason(e.target.value)}
                      rows={4}
                      className="text-sm"
                    />
                    {reportReason.trim().length > 0 && reportReason.trim().length < 10 && (
                        <p className="text-xs text-destructive">举报原因至少需要10个字符。</p>
                    )}
                  </div>
                  <AlertDialogFooter>
                    <AlertDialogCancel onClick={() => {setReportReason(''); setIsReportDialogOpen(false);}}>取消</AlertDialogCancel>
                    <AlertDialogAction 
                      onClick={handleReportSubmit} 
                      disabled={isReporting || reportReason.trim().length < 10}
                      className={cn(isReporting && "opacity-50 cursor-not-allowed")}
                    >
                      {isReporting ? <Loader2 size={16} className="animate-spin mr-2" /> : null}
                      提交举报
                    </AlertDialogAction>
                  </AlertDialogFooter>
                </AlertDialogContent>
              </AlertDialog>
            )}
            <Button 
              size="sm" 
              variant="ghost" 
              className={cn(
                "px-2 py-1 h-auto text-sm",
                isViewable ? "text-primary hover:bg-primary/10 hover:text-primary" : "text-muted-foreground cursor-not-allowed"
              )}
              disabled={!isViewable}
              aria-hidden={!isViewable}
              tabIndex={isViewable ? 0 : -1}
            >
              {buttonText}
              {isViewable && <ArrowRight size={16} className="ml-1" />}
            </Button>
        </div>
      </CardFooter>
    </Card>
  );
}

